/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include ipad-portrait {
	
	.mkdf-woocommerce-page {
		
		table.cart {
			
			td.actions {
				
				.coupon {
					width: 100%;
					float: none;

					input[type="text"] {
						width: 180px;
						margin: 0;
						padding: 9px 20px;
					}

					+ .button {
						float: left;
						margin-top: 20px;
					}
				}

				.button {
					display: block;
				}
			}
		}
	}
}

@include phone-landscape {
	
	.mkdf-woocommerce-page {

		table.cart {

			thead tr th {
				font-size: 10px;
				text-align: center;
			}

			tr {
				
				td {
					font-size: 12px;
					padding: 25px 5px !important;
					text-align: center;
				}
				
				&.cart_item {
					
					td {
						
						&.product-thumbnail {
							width: auto;
							padding-right: 0;
							
							img {
								display: none;
							}
						}
					}
				}
			}
			
			td.actions {
				
				.coupon {
					input[type="text"] {
						width: 160px;
						padding: 9px 15px;
						margin: 0 10px 20px 0;
					}
				}

				.button {
					margin: 20px 0 0;
				}
				
				.coupon,
				> input {
					float: none;
					display: block;
				}
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */