/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - begin
   ========================================================================== */

@include ipad-portrait {

    .woocommerce-page .mkdf-content,
    div.woocommerce {

        .mkdf-quantity-buttons {

            width: 150px;

            label.screen-reader-text {
                text-align: center;
            }

            .mkdf-quantity-input,
            .mkdf-quantity-minus,
            .mkdf-quantity-plus {
                font-size: 13px;
            }

            .mkdf-quantity-input {
                width: 26px;
                margin: 0;
                line-height: 36px;
                left: 30px;
            }

            .mkdf-quantity-plus {
                left: 30px;
            }

            .mkdf-quantity-minus {
                left: 30px;
            }
        }
    }
}   
/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - end
   ========================================================================== */